.header-color {
  background: #000000;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to bottom, #434343, #000000);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to bottom, #434343, #000000); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.landing-grid {
  background: url(https://elevateservices.com/wp-content/uploads/AdobeStock_208568531.jpeg);
  text-align: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.project-grid {
  display: flex;
  min-height: 85vh;
  justify-content: center;
  align-items: center;
}
.project-grid > div {
  margin: 20px;
}

.resume-grid {
  background: url((https://wallpaperaccess.com/full/301678.jpg);
}

/* Resume Page */
.resume-right-col {
  background: #1a1817;
  color: white;
  padding: 2em;
}

.contact-body {
  margin: auto;
  position: fixed;
  width: 100%;
  height: 100%;
  justify-content: center;
  padding-top: 2em;
}

.contact-grid {
  text-align: center;
  justify-content: center;
  width: 55%;
  height: 550px;
  background:white;
  color: black;
  margin-bottom: 20px;
}

.contact-grid h2 {
  font-family: 'Anton', sans-serif;
}

.contact-grid p {
  font-family: 'Oxygen', sans-serif;
  font-weight: bold;
}

.contact-grid hr {
  border-top: 5px dotted black;
  width: 50%;
  margin: auto;
}

.contact-list {
  width: 900px;
  margin-top: 3em;
}

.contact-list i {
  font-size: 66px;
  width: 30px;
  margin-right: 90px;
}

.contact > div {
  float: left;
}

.profile-photo{
  height: 350px;
  width: 350px;
  object-fit: cover;
  border-radius: 50%;
  margin-top: 5em;
}

.banner-text {
  opacity: .8;
  width: 75%;
  margin: auto;
  margin-top: 27em;
  border-radius: 10px;

}

  .banner-text h1 {
    font-size: 66px;
    font-weight: bold;
    color: white;
  }

  .banner-text hr {
    border-top: 5px dotted white;
    width: 50%;
    margin: auto;
  }

  .banner-text p {
    color: white;
    font-size: 20px;
    padding: 1em;
  }

  .social-links {
    display: flex;
    justify-content: left;
    width: 50%;
    padding-left: 70em;
  }

  .social-links i {
    color: white;
    font-size: 5em;
  }

  